import React from 'react';
import { Link, useHistory } from 'react-router-dom';

import {FiFacebook , FiInstagram , FiBookmark , FiTv, FiStar, 
        FiChevronsRight, FiHome, FiHeart, FiMoreVertical, 
        FiMessageSquare, FiLoader} from 'react-icons/fi';
import { MdFormatQuote } from 'react-icons/md';

import "animate.css/animate.min.css";
import ScrollAnimation from 'react-animate-on-scroll';

import emailjs from 'emailjs-com';

import './styles.css';

import val1 from '../../itens/val1-6.png';
import val2 from '../../itens/val5.png';

export default class About extends React.Component {
    constructor() {
        super();
        this.state = {
           className: 'menu',
           showMenu: false,
           name: '',
           email: '',
           message: '',
        }

        this.menuToggle = this.menuToggle.bind(this);
    }

    menuToggle = () => {
        this.setState({showMenu: !this.state.showMenu})
    }

    handleScroll() { 
        // console.log(document.documentElement.scrollTop);
        if (document.documentElement.scrollTop >= 50) {
            this.setState({
                className: 'menu-scrolled'
            })
        }
        else {
            this.setState({
                className: 'menu'
            })
        }
    }

   componentDidMount() {
    window.onscroll = () => this.handleScroll()
   }

    handleSubmit(e) {
        e.preventDefault()
 
         const { name, email, message } = this.state

         if(!name || !email || !message) {
            alert("Preencha todos os campos, por favor!");
         }

         else{

            let templateParams = {
                reply_to: email,
                from_name: name,
                to_name: 'Valesca', 
                message_html: message,
            }
    
            emailjs.send(
                'gmail',
                'template_vKhbHlBQ',
                 templateParams,
                'user_Mvckmf7YsyDIcuQvh8n0I'
            )
    
            .then((result) => {
                console.log(result.text);
                alert("Email enviado!");
            }, (error) => {
                console.log(error.text);
                alert("Ocorreu algum erro, tente novamente!");
            });
    
          
             this.resetForm()
         }
        }

     resetForm() {
        this.setState({
           name: '',
           email: '',
           message: '',
         })
      }
 
     handleChange = (param, e) => {
         this.setState({ [param]: e.target.value })
     }

    render(){
        return(
            <div className="total">

                <div className={this.state.className}>
                    <ul>
                        <div className="menu-logo">
                            <button onClick={this.menuToggle} value="toggle"><FiMoreVertical /></button>
                        </div>

                        <div className={this.state.showMenu ? 'showMenu' : 'hideMenu'}>
                            <li><a href="#home" onClick={this.menuToggle}><FiHome /> Início</a></li>
                            <li><a href="#um" onClick={this.menuToggle}><FiHeart /> Sobre</a></li>
                            <li><a href="#dois" onClick={this.menuToggle}><FiLoader /> Mural</a></li>
                            <li><a href="#contato" onClick={this.menuToggle}><FiMessageSquare /> Contato</a></li>

                            <h1><MdFormatQuote />Sou um mar de mistérios que ainda não fui capaz de decifrar.</h1>
                        </div>
                    </ul>
                </div>

                <div className="caixa-sobre">
                    <img src={val1} alt="perfil"/>
                    <h1>Valesca Soares</h1>
                    <div className="icons">
                        <a href="https://www.facebook.com/valescasoares2" target="_blank" rel="noopener noreferrer"><FiFacebook /></a>
                        <a href="https://www.instagram.com/valescasoares/" target="_blank" rel="noopener noreferrer"><FiInstagram /></a>
                        <a href="https://www.goodreads.com/val22k" target="_blank" rel="noopener noreferrer"><FiBookmark /></a>
                        <a href="https://www.tvshowtime.com/en/user/8323926/profile" target="_blank" rel="noopener noreferrer"><FiTv /></a>
                    </div>
                </div>

                <span id="home">-</span>
                <div className="fundo">
                    <ScrollAnimation animateIn='tada' initiallyVisible={true} animateOnce={true}>
                        <h1><MdFormatQuote />Sou um mar de mistérios que ainda não fui capaz de decifrar.</h1>
                    </ScrollAnimation>
                </div>

                <div className="caixa-conteudo">

                    <div id="um">-</div>
                    <h1>Quem sou?</h1>
                    <div className="alinha">
                        <ScrollAnimation animateIn='bounceInLeft' animateOnce={true}>
                            <div className="caixa-pequena">
                                <p>Minha família me chama de Valesca, meus amigos grande parte do tempo me chamam de Val
                                e meu namorado também, além de todos os outros apelidos carinhosos. Atualmente tenho 23
                                anos e nesse tempo já fui muitas versões diferentes de mim mesma, fui moldada por meus
                                sentimentos diante dos momentos em que vivi e pelas boas pessoas que passaram pela minha
                                vida. 

                                Eu poderia falar diversas qualidades e defeitos que tenho consciência de mim mesma, mas 
                                prefiro fazer isso de uma maneira diferente contando uma pequena história.

                                </p>

                            </div>
                        </ScrollAnimation>
                        <ScrollAnimation animateIn='bounceInRight' animateOnce={true}>
                            <div className="caixa-pequena">
                                <p>Era uma vez uma menininha que nasceu pequena e foi crescendo com o passar do tempo. Ela 
                                só era faladeira com pessoas que ela tinha mais apego, o tempo passou mais, ela teve 
                                experiências que foram um divisor de águas em sua vida e ela já não era tão tímida assim. 
                                Entretanto, ainda preferia a solidão.

                                <img src={val2} alt="sobre" style={{width: '170px', position:'relative', float:'right'}}/>
                                
                                </p>
                            </div>
                        </ScrollAnimation>
                    </div>
                
                    <div id="dois">-</div>
                    <h1>Mural</h1>
                    <div className="alinha">
                        <ScrollAnimation animateIn='bounceInLeft' animateOnce={true}>
                            <div className="caixa-pequena"></div>
                        </ScrollAnimation>
                        <ScrollAnimation animateIn='bounceInRight' animateOnce={true}>
                            <div className="caixa-pequena"></div>
                        </ScrollAnimation>
                    </div>
                    
                    <div className="alinha">
                        <ScrollAnimation animateIn='bounceInLeft' animateOnce={true}>
                            <div className="caixa-pequena"></div>
                        </ScrollAnimation>
                        <ScrollAnimation animateIn='bounceInRight' animateOnce={true}>
                            <div className="caixa-pequena"></div>
                        </ScrollAnimation>
                    </div>
                    
                    <br></br><br></br><br></br><br></br><br></br>
                </div>

                <div className="faixa"></div>

                <form onSubmit={this.handleSubmit.bind(this)}>
                    <div id="contato">-</div>
                    <h1>Contato</h1>
                    <input placeholder="Nome" value={this.state.name} onChange={this.handleChange.bind(this, 'name')}></input>
                    <input placeholder="Email" value={this.state.email} type="email" onChange={this.handleChange.bind(this, 'email')}></input>
                    <textarea placeholder="Mensagem" value={this.state.message} onChange={this.handleChange.bind(this, 'message')}></textarea>
                    <button type="submit">Enviar</button>
                </form>

                <div className="faixa"></div>

                <footer>
                    <FiStar /> Feito por Valesca Soares em 2020 <FiChevronsRight /> Brasília, Brasil. 
                </footer>
            
            </div>
        );
    }
}